import React, { useState, useEffect } from "react";
import { DatePicker } from "antd";
import moment from "moment/moment";
import "antd/dist/antd.css";
import "./File.css";
import { stringify } from "rc-field-form/es/useWatch";

export default function Files() {
  const [date, setdate] = useState(new Date());

  const [mystyle, setmystyle] = useState({
    display: "block",
  });
  const [mystyle2, setmystyle2] = useState({
    display: "block",
  });

  const [value, setvalue] = useState("");
  const [result, setresult] = useState([]);
  const schange = () => {
    if (value.length > 0) {
      setmystyle({
        display: "none",
      });
      setmystyle2({
        display: "block",
      });
    } else {
      setmystyle({
        display: "block",
      });
      setmystyle2({
        display: "none",
      });
    }
  };
  useEffect(() => {
    if (value.length > 0) {
      fetch("https://test-task-755ab-default-rtdb.firebaseio.com/qwerty.json")
        .then((response) => response.json())
        .then((responseData) => {
          setresult([]);
          let searchquery = value.toLowerCase();

          for (const key in responseData) {
            let fruit = responseData[key].name.toLowerCase();
            if (
              fruit.slice(0, searchquery.length).indexOf(searchquery) !== -1
            ) {
              setresult((Prevresult) => {
                return [...Prevresult, responseData[key].name];
              });
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      setresult([]);
    }
  }, [value]);

  return (
    <div className="body">
      <div className="dropbox container ">
        <div className="empheading">Select employee dropdown</div>
        <div className="dropdown m-5  ">
          <button
            className="btn btn-secondary dropdown-toggle btn-dark btndown"
            type="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            Select employee
          </button>
          <ul className="dropdown-menu  dropmenue">
            <li>
              <input
                className="searchdrop "
                onChange={(event) => setvalue(event.target.value)}
                onKeyUp={schange}
                value={value}
                id="search"
                type="text"
                placeholder="      Search employee..."
              />
            </li>
            <div style={mystyle}>
              <li>
                <a className="dropdown-item allemp" href="#">
                  All employee{" "}
                </a>
                <input
                  className="form-check-input chakekbox"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <hr className="line10" />
              <li>
                <a className="dropdown-item name1" href="#">
                  Dr. Siegfried Tausend
                </a>
                <input
                  className="form-check-input chakekbox2"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <hr className="line11" />
              <li>
                <label className="label">All practitioners</label>
                <input
                  className="form-check-input chakekbox3"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <li>
                <a className="dropdown-item name2" href="#">
                  Dr. Xavier III
                </a>
                <input
                  className="form-check-input chakekbox4"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <li>
                <a className="dropdown-item name3" href="#">
                  Dr. Don Health
                </a>
                <input
                  className="form-check-input chakekbox5"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <li>
                <a className="dropdown-item name4" href="#">
                  Dr. Siegfried Tausend
                </a>
                <input
                  className="form-check-input chakekbox6"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
              <hr className="line12" />
              <li>
                <label className="label2">All assistants</label>
                <input
                  className="form-check-input chakekbox7"
                  type="checkbox"
                  value=""
                  id="flexCheckDefault"
                ></input>
              </li>
            </div>
          </ul>
        </div>
      </div>

      <div classnames="container">
        {result.map((result, index) => (
          <a href="#" key={index}>
            <div className="searchentry" style={mystyle2}>
              {result}
            </div>
          </a>
        ))}
      </div>

      <div className="container">
        <div className="empheading2 container">Pick date</div>
        <DatePicker
          style={{ Color: "red" }}
          onChange={(value) => {
            setdate(value);
            const value1 = moment(value[0]).format("DD-MM-YYYY");
            {
              console.log(value1);
            }
          }}
        ></DatePicker>
      </div>
    </div>
  );
}
